import { Component } from '@angular/core';
import { NavController, AlertController, LoadingController } from 'ionic-angular';

import { OneSignal } from '@ionic-native/onesignal';

import { HttpClient } from '@angular/common/http';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import { Camera, CameraOptions } from '@ionic-native/camera';

import { HTTP } from '@ionic-native/http';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

 public thisversion : any = 1;
 public feed : any[];
 public me : any = 0;
 public nick : any = '';
 public hmode : any = 0;
 pl : any;
 reg : any;
 public manager : any = false;
  constructor(public navCtrl: NavController, private http : HttpClient, private iab : InAppBrowser, private camera: Camera, public loadingCtrl: LoadingController, public nhttp: HTTP, public alertCtrl: AlertController, private oneSignal: OneSignal) {
    this.feed = [];
    this.manager = false;
    this.hmode=0;
    this.thisversion = 3; 

    this.pl = {
      picdata : '',
      id : 0
    };

    this.reg = {
      code : '',
      username : ''
    };

  }


  ionViewDidEnter()  {
     var tme = window.localStorage.getItem('me');
     if (!tme) tme='0';
     this.me = parseInt(tme);
  
  }

  takepic()
  {  this.pl.id = this.me;
     const options: CameraOptions = {
        quality: 100,
        targetWidth:500,
        targetHeight:650, 
        correctOrientation:true,
        destinationType: this.camera.DestinationType.DATA_URL,
        cameraDirection: this.camera.Direction.FRONT,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE
      }
      this.camera.getPicture(options).then((imageData) => {
        this.pl.picdata = imageData;
        this.saveme();
      }, (err) => {
         alert(JSON.stringify(err));
      });
 
  }

  saveme()
  {
      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
   

       let url = 'https://jkur.com.au/hungry/upload.php';

         this.nhttp.post(url, that.pl, {})
         .then(data => {
            loading.dismiss();
 	    that.doComplete();
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
     

  }

  doRegister()
  {
      let that = this;
       let loading = this.loadingCtrl.create({
         content: 'Please wait...'
       });
       loading.present();
   

       let url = 'https://jkur.com.au/hungry/applogin.php';

         this.nhttp.post(url, that.reg, {})
         .then(data => {
            loading.dismiss();
            let s = JSON.stringify(data);
            let d = JSON.parse(s);
            this.me = parseInt(d.id);
            this.nick = d.username;   
         })
         .catch(error => {
            alert(JSON.stringify(error));
            loading.dismiss();
     
         });
     

  }



  signOutAlert() {
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'Are you sure?',
      subTitle: '',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Yes',
          role: 'cancel',
          handler: () => {
   //         that.doLogout();
          }
        }
      ]
    });
    alert.present();
 }


  versionmessage(a) {
      this.thisversion = a;
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'New Version Available',
      subTitle: 'Please go to the App Store or Google Play Store to get the latest version of this App',
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
          }
        }
        
      ]
    });
    alert.present();
 }

  makebook() {
      var burl = 'https://www.vcita.com/v/a7b1ef853d6e42ef//online_scheduling?&o=bGl2ZXNpdGVfbWVudQ%3D%3D&s=https%3A%2F%2Fhungrycoach.com%2F&&vitrage_iframe=false&portal_iframe=false#/services';
      const browser = this.iab.create(burl,'_blank','location=yes');
  }

  doComplete() {
    let that=this;
    let alert = this.alertCtrl.create({
      title: 'Thank you',
      subTitle: 'Your photo has been sent',
      buttons: [
        {
          text: 'Okay',
          cssClass: 'btt',
	  role: 'cancel',
          handler: () => {
          }
        }
      ]
    });
    alert.present();
  }


}





